@tailwind base;
@tailwind components;
.filter-message .error {
  color: #d73a49;
}
.filter-message .valid {
  color: #22863a;
}
.filter .unexpected {
  border-bottom: solid 2px;
  border-color: #d73a49;
}
.filter .token.Token_Empty_Str,
.filter .token.Token_Incomplete_Str,
.filter .token.Token_String,
.filter .token.Token_Term {
  color: darkorange;
}
.filter .token.Token_Error {
  color: #d73a49;
}
.filter .token.Token_Open_Backet,
.filter .token.Token_Close_Bracket,
.filter .token.Token_Open_Angled_Bracket,
.filter .token.Token_Close_Angled_Bracket,
.filter .token.Token_Coma,
.filter .token.Token_Facet_Separator {
  color: #0044dd;
}
.filter .token.Token_Num {
  color: #6f42c1;
}
.filter .token.Token_OR,
.filter .token.Token_AND,
.filter .token.Token_NOT,
.filter .token.Token_Operator,
.filter .token.Token_Range {
  color: #22863a;
}
.filter .Token_EOF {
  @apply text-transparent;
}

/**
 * prism.js Github theme based on GitHub's theme.
 * @author Sam Clarke
 */
code[class*=language-],
pre[class*=language-] {
  color: #333;
  background: none;
  font-family: Consolas, "Liberation Mono", Menlo, Courier, monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.4;
  -moz-tab-size: 8;
  -o-tab-size: 8;
  tab-size: 8;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

/* Code blocks */
pre[class*=language-] {
  padding: 0.8em;
  overflow: auto;
  border: 1px solid #ddd;
  border-radius: 3px;
  background: #fff;
}

/* Inline code */
:not(pre) > code[class*=language-] {
  padding: 0.1em;
  border-radius: 0.3em;
  white-space: normal;
  background: #f5f5f5;
}

.token.comment,
.token.blockquote {
  color: #969896;
}

.token.cdata {
  color: #183691;
}

.token.doctype,
.token.punctuation,
.token.variable,
.token.macro.property {
  color: #333;
}

.token.operator,
.token.important,
.token.keyword,
.token.rule,
.token.builtin {
  color: #a71d5d;
}

.token.string,
.token.url,
.token.regex,
.token.attr-value {
  color: #183691;
}

.token.property,
.token.number,
.token.boolean,
.token.entity,
.token.atrule,
.token.constant,
.token.symbol,
.token.command,
.token.code {
  color: #0086b3;
}

.token.tag,
.token.selector,
.token.prolog {
  color: #63a35c;
}

.token.function,
.token.namespace,
.token.pseudo-element,
.token.class,
.token.class-name,
.token.pseudo-class,
.token.id,
.token.url-reference .token.variable,
.token.attr-name {
  color: #795da3;
}

.token.entity {
  cursor: help;
}

.token.title,
.token.title .token.punctuation {
  font-weight: bold;
  color: #1d3e81;
}

.token.list {
  color: #ed6a43;
}

.token.inserted {
  background-color: #eaffea;
  color: #55a532;
}

.token.deleted {
  background-color: #ffecec;
  color: #bd2c00;
}

.token.bold {
  font-weight: bold;
}

.token.italic {
  font-style: italic;
}

/* JSON */
.language-json .token.property {
  color: #183691;
}

.language-markup .token.tag .token.punctuation {
  color: #333;
}

/* CSS */
code.language-css,
.language-css .token.function {
  color: #0086b3;
}

/* YAML */
.language-yaml .token.atrule {
  color: #63a35c;
}

code.language-yaml {
  color: #183691;
}

/* Ruby */
.language-ruby .token.function {
  color: #333;
}

/* Markdown */
.language-markdown .token.url {
  color: #795da3;
}

/* Makefile */
.language-makefile .token.symbol {
  color: #795da3;
}

.language-makefile .token.variable {
  color: #183691;
}

.language-makefile .token.builtin {
  color: #0086b3;
}

/* Bash */
.language-bash .token.keyword {
  color: #0086b3;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

html {
  line-height: 1.15;
}

img, svg, video, canvas, audio, iframe, embed, object {
  display: inline;
  vertical-align: middle;
}

h1 {
  font-size: 2em;
  font-weight: bolder;
}

h2 {
  font-size: 1.5em;
  font-weight: bolder;
}

h3 {
  font-size: 1.17em;
  font-weight: bolder;
}

h4 {
  font-size: 1em;
  font-weight: bolder;
}

h5 {
  font-size: 0.83em;
  font-weight: bolder;
}

h6 {
  font-size: 0.75em;
  font-weight: bolder;
}

input, input:focus, button, button:focus {
  outline: none;
}

.input-custom {
  @apply flex items-center rounded text-sm bg-white shadow pl-8 outline-none;
}
.input-custom:hover {
  @apply shadow-md;
}

.infinte-rotate {
  -webkit-animation: infinte-rotate 4s infinite linear;
  animation: infinte-rotate 4s infinite linear;
}

@-webkit-keyframes infinte-rotate {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}
@keyframes infinte-rotate {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}
.autosuggest__results_item-highlighted {
  border: solid 1px red;
}

.dropdown em {
  text-decoration: underline;
  font-style: normal;
}

.hit em, .ais-Highlight mark {
  background: rgba(84, 104, 255, 0.1);
  color: #5468ff;
  font-style: normal;
}

.hit.link em {
  color: #0c37ff;
}

.json-token.string {
  @apply text-cosmos-black-opacity-70;
}

.json-token.boolean {
  @apply text-neptune-0;
}

.json-token.number {
  word-break: break-word;
  @apply text-neptune-0;
}

.json-token.null {
  @apply text-neptune-0;
}

.json-token-wrapper {
  color: gray;
}

pre {
  margin: 0;
  font-family: Times, sans-serif;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.inset-x-auto {
  left: 50%;
  transform: translateX(-50%);
}

.inset-y-auto {
  top: 50%;
  transform: translateY(-50%);
}

.inset-y-full {
  top: 0;
  transform: translateY(-100%);
}

.inset-x-auto-y-full {
  top: 0;
  left: 50%;
  transform: translate(-50%, -100%);
}

.inset-x-full-y-auto {
  top: 50%;
  right: 0;
  transform: translate(100%, -50%);
}

@tailwind utilities;
@-moz-document url-prefix() {
  .h-1 {
    height: 100%;
    min-height: 1px;
  }
}